<template>
  <div id="roleManage">
    <QueryBox @searchSrldId="searchSrldId">
      <Button type="primary" size="small" style="float: right" @click.stop="$refs.roleAdd.build = true">新建</Button>
    </QueryBox>
    <div class="tab_box">
      <Table ref="table" :columns="columns1" :data="data1"  size="small" :height="tableHeight" ></Table>
    </div>
    <div class="page_box">
      <Page :total="total" show-elevator size="small" @on-change="pageChange"/>
    </div>
    <RoleAdd ref="roleAdd" :resList="resList"  @getTableData="getTableData"></RoleAdd>
    <RoleEdit ref="roleEdit" :data="editDefaultData" @getTableData="getTableData"></RoleEdit>
  </div>
</template>

<script>
import { renderTime } from '@/utils/common'
import { list, roleDelete, resourceList } from '@/api/systemManage'
import RoleAdd from './Role_add'
import RoleEdit from './Role_edit'
import QueryBox from './Role_query'
export default {
  name: 'RoleManage',
  data () {
    return {
      /* 查询条件 */
      code: '',
      page: 1,
      limit: 10,
      realName: '',
      phoneNumber: '',
      department: '',
      /* 表格动态高度 */
      tableHeight: Math.floor((window.innerHeight - 288 - 32) / 40) * 40 + 32,
      /* 每页条数 */
      pageSize: Math.floor(Math.floor((window.innerHeight - 288 - 32) * 40 + 32) / 40) - 1,
      /* 页码 */
      pageNum: '',
      /* 页面总条数 */
      total: 0,
      /* 删除数据id */
      delId: '',
      /* 编辑框默认原数据 */
      editDefaultData: {},
      /* 表格 */
      columns1: [
        {
          title: '角色id',
          key: 'id'
        },
        {
          title: '角色名称',
          key: 'name'
        },
        {
          title: '创建时间',
          key: 'createDate',
          render: (h, params) => {
            return h('span', {}, renderTime(params.row.createDate))
          }
        },
        {
          title: '创建人',
          key: 'createUser'
        },
        {
          title: '更改时间',
          key: 'updateDate',
          render: (h, params) => {
            return h('span', {}, renderTime(params.row.updateDate))
          }
        },
        {
          title: '更改人',
          key: 'updateUser'
        },
        {
          title: '备注',
          key: 'remarks'
        },
        {
          title: '操作',
          key: 'action',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editDefaultData = params.row
                    this.$refs.roleEdit.edit = true
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该用户么？',
                      onOk: () => {
                        this.roleDelete(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 行政区划数据 */
      codeData: [],
      /* 部门列表 */
      departmentList: [],
      /* 全部资源列表 */
      resList: [],
      /* 查询条件 */
      name: ''
    }
  },
  components: {
    RoleAdd,
    RoleEdit,
    QueryBox
  },
  mounted () {
    this.getTableData()
    this.resourceList()
  },
  methods: {
    /* axios: 获取列表数据 */
    getTableData () {
      list({
        data: {
          name: this.name
        }
      }).then((res) => {
        this.data1 = res.data.data.rows
        this.total = res.data.data.total
      }).catch((error) => {
        console.log(error)
      })
    },
    /* axios: 获取列表数据 */
    resourceList () {
      resourceList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.resList = res.data.data
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    /* axios: 删除数据 */
    roleDelete (id) {
      roleDelete({
        data: {
          id: id
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableData()
          this.$Message.info('删除成功')
        } else {
          this.$Message.info('删除失败')
        }
      }).catch(() => {
        this.$Message.info('error')
      })
    },
    /* 查询框 */
    searchSrldId (obj) {
      this.name = obj.realName
      this.getTableData()
    },
    /* 分页跳转 */
    pageChange (p) {
      this.page = p
    }
  }
}
</script>

<style scoped lang="scss">
  #roleManage{
    .from_box{
      padding:10px;
      text-align: left;
    }
    .tab_box{
      padding: 10px;
    }
    .page_box{
      text-align: right;
      padding:0 10px
    }
  }
</style>
