<template>
  <div id="role_add">
    <Modal
      v-model="build"
      title="新建"
      @on-cancel="AddData = {}"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="name" label="角色名称：">
          <Input v-model.trim="AddData.name" placeholder="请输入角色姓名" size="small"/>
        </FormItem>
        <FormItem prop="createUser" label="创建人：">
          admin
        </FormItem>
        <FormItem prop="createUser" label="功能权限：">
          <Tree :data="resList" show-checkbox multiple ref="tree"></Tree>
        </FormItem>
        <FormItem prop="remarks" label="备注：">
          <Input v-model.trim="AddData.remarks" placeholder="请输入备注" size="small"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { roleAdd } from '@/api/systemManage'
export default {
  name: 'UserAdd',
  data () {
    return {
      /* 添加数据 */
      AddData: {
      },
      AddRule: {
        name: [
          { required: true, message: '姓名不能为空', trigger: 'blur' }
        ]
      },
      /* 新建框 */
      build: false
    }
  },
  props: {
    /* 全部资源列表 */
    resList: {}
  },
  mounted () {
  },
  methods: {
    /* axios: 添加数据 */
    roleAdd () {
      let resArr = []
      for (let item of this.$refs.tree.getCheckedNodes()) {
        resArr.push(item.id)
      }
      roleAdd({
        data: {
          name: this.AddData.name,
          id: this.$store.getters.getloginInfor().userId,
          resourceIds: resArr,
          remarks: this.AddData.remarks
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$emit('getTableData')
          this.$Message.info('添加成功')
          this.build = false
          this.AddData = {}
        } else {
          this.$Message.info(res.data.msg)
        }
      }).catch((error) => {
        this.$Message.info('error')
        console.log(error)
      })
    },
    /* 添加表单验证 */
    addSubmit () {
      this.$refs['add'].validate((valid) => {
        if (valid) {
          this.roleAdd()
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
</style>
