<template>
  <div id="userEdit">
    <Modal
      v-model="edit"
      title="编辑">
      <Form ref="edit" :model="EditData" :rules="EditRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="id" label="ID：">
          {{EditData.id}}
        </FormItem>
        <FormItem prop="name" label="姓名：">
          <Input v-model.trim="EditData.name" placeholder="请输入姓名" size="small"/>
        </FormItem>
        <FormItem label="更改人：">
          admin
        </FormItem>
        <FormItem prop="createUser" label="功能权限：">
          <Tree :data="resList" show-checkbox multiple ref="tree"></Tree>
        </FormItem>
        <FormItem label="备注：">
          <Input v-model.trim="EditData.remarks" placeholder="请输入备注" size="small"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { roleEdit, resourceList } from '@/api/systemManage'
export default {
  name: 'UserEdit',
  data () {
    return {
      EditRule: {
        name: [
          { required: true, message: '姓名不能为空', trigger: 'blur' }
        ]
      },
      /* 编辑框 */
      edit: false,
      /* 编辑数据 */
      EditData: {},
      resList: []
    }
  },
  props: {
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  mounted () {
  },
  methods: {
    /* axios: 修改数据 */
    roleEdit () {
      let resArr = []
      for (let item of this.$refs.tree.getCheckedAndIndeterminateNodes()) {
        resArr.push(item.id)
      }
      roleEdit({
        data: {
          updateBy: this.$store.getters.getloginInfor().userId,
          id: this.EditData.id,
          name: this.EditData.name,
          remarks: this.EditData.remarks,
          resourceIds: resArr
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$emit('getTableData')
          this.$Message.info('修改成功')
        } else {
          this.$Message.info('修改失败')
        }
      }).catch((error) => {
        console.log(error)
        this.$Message.info('error')
      })
    },
    /* axios: 获取列表数据 */
    resourceList () {
      resourceList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          let defArr = []
          for (let item of this.data.resourcesSet) {
            defArr.push(item.id)
          }
          let data = res.data.data
          data = this.ergodicTree(data, defArr)
          this.resList = data
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    /* 编辑框默认值数据处理 */
    dataHandle () {
      this.EditData.id = this.data.id
      this.EditData.name = this.data.name
      let defArr = []
      for (let item of this.data.resourcesSet) {
        defArr.push(item.id)
      }
      console.log(this.resList)
      this.resList = []
      this.resourceList()
      // this.ergodicTree(this.resList, defArr)
    },
    /* 遍历树 */
    ergodicTree (data, defArr) {
      for (let i in data) {
        if (data[i].children.length === 0 && defArr.indexOf(data[i].id) !== -1) {
          data[i].checked = true
        } else {
          this.ergodicTree(data[i].children, defArr)
        }
      }
      return data
    },
    /* 修改表单验证 */
    editSubmit () {
      this.$refs['edit'].validate((valid) => {
        if (valid) {
          this.roleEdit()
          this.edit = false
        }
      })
    }
  }
}
</script>

